import React from 'react'
import arrows from '../assets/images/arrows.svg'
import Card from "../components/Card";
import Cardb from "../components/Cardb";
import projectImage from '../assets/images/Project.jpg';
import { PencilAltIcon } from "@heroicons/react/outline";
import { UserIcon } from "@heroicons/react/outline";
import { PaperAirplaneIcon } from "@heroicons/react/outline";
import { CodeIcon } from "@heroicons/react/outline";

function Body1() {
  return (
    <div className='flex flex-col items-center justify-center my-8'>
        <div className='md:grid grid-cols-2 justify-center items-center'>
        <div className='flex flex-col items-center my-4 md:my-0'>
            <h1 className='text-2xl font-bold my-2'>My Awesome Services</h1>
            <img src={arrows} alt="/" className='w-5 my-4'/>
            <img src={projectImage} alt="/" className='rounded-lg w-40 md:w-60' />
        </div>
        <div className='ml-20 md:flex items-center justify-between'>
            <div className=''>
            <Cardb 
                icon= {<PencilAltIcon className='w-10 h-10 rounded-xl text-indigo-800 bg-white px-2 animate-pulse'/>}
                title="User Interface Designer"
                subtitle="I designer what user can easily interact with"
            />
            <Card 
                icon={<UserIcon className='w-10 h-10 rounded-xl text-white bg-indigo-800 px-2 animate-pulse'/>}
                title="User Experience Designer"
                subtitle="I designer what user can easily interact with"
            />
            </div>
            <div>
            <Card 
                icon={<PaperAirplaneIcon className='w-10 h-10 rounded-xl text-white bg-indigo-800 px-2 animate-pulse' />}
                title="User Experience Writer"
                subtitle="I designer what user can easily interact with"
            />
            <Card 
                icon={<CodeIcon className='w-10 h-10 rounded-xl text-white bg-indigo-800 px-2 animate-pulse'/>}
                title="Frontend Developer"
                subtitle="I designer what user can easily interact with"
            />
            </div>
            </div>
        </div>
    </div>
  )
}

export default Body1