import React from 'react'

function Pcard(props) {
  return (
    <div className='bg-white rounded-xl px-4 py-4 w-fit shadow-xl items-center my-2 mx-4'>
        <img src={props.img} className='w-60 h-40 rounded-xl'/>
        <div className='flex justify-between items-center my-1'>
            <h1>{props.title}</h1>
            <p className='text-tiny rounded-xl bg-indigo-500 text-white p-1'>{props.badge}</p>
        </div>
        <div>
            <p className='w-60 text-xs my-2'>{props.description}</p>
        </div>
        <div className=''>
            <button className='px-3 py-1 text-sm w-full mt-4'>View Project</button>
        </div>
    </div>
  )
}

export default Pcard