import React from 'react'

function Hcard(props) {
  return (
    <div>
        <div className='bg-white rounded-xl px-4 py-4 w-fit shadow-xl flex items-center my-2 mx-4'>
            <div>
                {props.icon}
            </div>
            <div className='mx-4'>
                <h1 className='font-bold text-xl'>{props.title}</h1>
                <p className='text-sm w-80'>{props.subtitle}</p>
            </div>
        </div>
    </div>
  )
}

export default Hcard