import React from 'react'

function Cardb(props) {
  return (
    <div>
        <div className='bg-indigo-800 rounded-xl w-3/4 px-6 py-4 shadow-xl my-2'>
            {props.icon}
            <h1 className='text-xl w-40 text-white'>{props.title}</h1>
            <p className='text-sm text-white my-1'>{props.subtitle}</p>
        </div>
    </div>
  )
}

export default Cardb