import React from 'react'
import Hcard from "../components/Hcard";
import {PlusCircleIcon} from '@heroicons/react/outline'
import projectImage from '../assets/images/Project.jpg';
import arrows from '../assets/images/arrows.svg'

function Body2() {
  return (
<div className='flex flex-col items-center justify-center my-8'>
        <div className='md:grid grid-cols-2 justify-center items-center'>
        <div className='md:block items-center'>
        <Hcard 
        icon={<PlusCircleIcon className='w-10 h-10 rounded-xl text-white bg-indigo-800 px-2 animate-spin'/>}
        title="Experience"
        subtitle="For every page visitor, there must 
        be an aim for visiting the page lets 
        find out yours."
      />
      <Hcard 
        icon={<PlusCircleIcon className='w-10 h-10 rounded-xl text-white bg-indigo-800 px-2 animate-spin'/>}
        title="Team Engagement"
        subtitle="For every page visitor, there must 
        be an aim for visiting the page lets 
        find out yours."
      />
      <Hcard 
        icon={<PlusCircleIcon className='w-10 h-10 rounded-xl text-white bg-indigo-800 px-2 animate-spin'/>}
        title="Multi-tasking"
        subtitle="For every page visitor, there must 
        be an aim for visiting the page lets 
        find out yours."
      />
      <Hcard 
        icon={<PlusCircleIcon className='w-10 h-10 rounded-xl text-white bg-indigo-800 px-2 animate-spin'/>}
        title="Time Management"
        subtitle="For every page visitor, there must 
        be an aim for visiting the page lets 
        find out yours."
      />
      <Hcard 
        icon={<PlusCircleIcon className='w-10 h-10 rounded-xl text-white bg-indigo-800 px-2 animate-spin'/>}
        title="Communication"
        subtitle="For every page visitor, there must 
        be an aim for visiting the page lets 
        find out yours."
      />
            </div>
            <div className='flex flex-col items-center my-4 md:my-0'>
                <h1 className='text-2xl font-bold my-2'>WHY YOU SHOULD HIRE ME</h1>
                <img src={arrows} alt="/" className='w-5 my-4'/>
                <img src={projectImage} alt="/" className='rounded-lg w-40 md:w-60' />
        </div>
        </div>
    </div>
  )
}

export default Body2