import React, {useState} from 'react'
import {MenuIcon, XIcon} from '@heroicons/react/outline'




function Navbar() {
  const [nav, setNav] = useState(false)
const handleClick = () => setNav(!nav)

  return (
    <div className='w-screen h-{100px} bg-zinc-200 z-10 fixed drop-shadow-lg'>
      <div className='px-2 flex justify-between md:justify-around items-center w-full h-full'>
        <div className='flex items-center'>
          <h1 className='text-3xl font-bold mr-4 sm:text-4xl text-indigo-800'>HUNAIS.</h1>
        </div>
        <div className='flex items-center'>
          <ul className='hidden md:flex'>
              <li>Home</li>
              <li>About</li>
              <li>Featured</li>
              <li>Blog</li>
            </ul>
        </div>
        <div className='items-center hidden md:flex'>
          <button className='border-none bg-transparent text-black mr-4'>Download CV</button>
          <button className='px-6 py-3'>Contact</button>
        </div>
        <div className='md:hidden' onClick={handleClick}>
        {!nav ? <MenuIcon className='w-5 cursor-pointer' /> : <XIcon className='w-5 cursor-pointer' />}
      </div>
      </div>

      <ul className={!nav ? 'hidden' : 'absolute bg-zinc-200 w-full px-8'}>
        <li className='border-b-2 border-zinc-300 w-full'>Home</li>
        <li className='border-b-2 border-zinc-300 w-full'>About</li>
        <li className='border-b-2 border-zinc-300 w-full'>Featured</li>
        <li className='border-b-2 border-zinc-300 w-full'>Blog</li>

        <div className='flex flex-col my-3'>
          <button className='px-6 py-3 my-2 bg-transparent text-black'>Download CV</button>
          <button className='px-6 py-3'>Contact</button>
        </div>
      </ul>
    </div>
  )
}

export default Navbar