import React from 'react'

function card(props) {
  return (
    <div className='bg-white rounded-xl w-3/4 px-6 py-4 shadow-xl my-2'>
        {props.icon}
        <h1 className='text-xl w-40'>{props.title}</h1>
        <p className='text-sm my-1'>{props.subtitle}</p>
    </div>
  )
}

export default card