import React from 'react'
import { UserIcon } from "@heroicons/react/outline";
import projectImage from '../assets/images/Project.jpg';

function Rcard(props) {
  return (
    <div className='flex flex-col items-center my-2 mx-4'>
        <div className='shadow-xl bg-white p-2 w-fit rounded-lg -mb-1 relative'>
            <img src={props.image} className='w-5 rounded-xl'/>
        </div>
        <div className='shadow-xl bg-white p-8 w-fit rounded-xl'>
            <h2 className='w-80 text-xs'>
                {props.text}
            </h2>
            <div className='my-2'>
                <p className='text-xs ml-40'>{props.position}</p>
                <p className='text-xs ml-40'>{props.name}</p>
            </div>
        </div>
    </div>
  )
}

export default Rcard